

.vieme-short {
    display: block;
    margin: 16px 0;
    background-color: #fff;
}

.header-vieme-short {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.header-vieme-short .title {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 3px solid blue;
    font-size: 12px;
    font-weight: 500;
}

.container-short {
    display: flex;
    padding: 0 8px;
    margin: 18px 0;
    overflow-x: scroll;
}

.container-short .item-short {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 16px;
}

/* .container-short .item-short */

.container-short .item-short .box-short img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.container-short .item-short .name-account {
    font-size: 10px;
    margin-top: 4px;
}

.container-short .item-short .box-short {
    width: 60px;
    height: 60px;
    padding: 2px;
    border: 2px solid blueviolet;
    border-radius: 50%;
    position: relative;
}

.container-short .item-short:first-child .box-short{
    border: 0;
}

.container-short .item-short .box-short .svg-inline--fa {
    position: absolute;
    bottom: 0;
    right: 0;
    color: blue;
}


