
.footer-mobile {
    position: fixed;
    bottom: 0;
    height: 44px;
    width: 100%;
    background-color: blueviolet;
}

.footer-mobile .container {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    height: 44px;
}

.footer-mobile .container .item-footer {
    display: flex;
    align-items: center;
    flex-direction: column;
    color: #fff;
}

.footer-mobile .container .item-footer:nth-child(3) .svg-inline--fa {
    font-size: 28px;
}

.footer-mobile .container .item-footer .svg-inline--fa {
    font-size: 20px;
}

.footer-mobile .container .item-footer .title {
    font-size: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 2px;
}