:root {
    --heightHeader: 56px;
    --heightSearchFormHeader: 36px;
    --font-SizeIconHeader: 20px;
    --coloriconHeader: #dcedf8;
    --marginHeader: 8px;
    --margin-IconHeader: 16px;
}

/* Responsive tablet */
@media (max-width: 1024px)  {
    :root {
        --marginHeader: 16px;
    }
}

/* Responsive mobile */
@media (max-width: 767px)  {
    :root {
        --marginHeader: 8px;
        --margin-IconHeader: 8px;
        --font-SizeIconHeader: 18px;
    }

    .header-vieme .header-left .Name-Company h1{
        color: #ffffff;
        font-size: var(--font-SizeIconHeader) !important;
        margin-right: 8px;
    }

    .header-vieme .header-center .svg-inline--fa {
        font-size: var(--font-SizeIconHeader) !important;
        margin: 0 var(--margin-IconHeader) 0 var(--margin-IconHeader);
        color: var(--coloriconHeader);
        display: flex;
        justify-content: end;
        align-items: center;
        flex-direction: row;
    }

    .header-vieme .header-center .item-centerHeader {
        display: block !important;
    }

    .header-vieme .header-center {
        display: flex;
        justify-content: end;
    }

    .header-vieme .header-right .avatar-circle {
        width: 24px !important;
        height: 24px !important;
    }

    .header-vieme .logo-header {
        padding: 8px !important;
    }

    .SearchForm {
        display: none !important;
    }

    .header-right .hint-button {
        display: none;
    }
}



