
.post-form {
    width: 100%;
    padding: 0 8px;
    background-color:  #fff;
}

.header-formPost {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.post-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.post-container .title {
    margin-left: 16px;
    font-size: 14px;
    font-weight: 600;
}


.avatar-post {
    height: 40px;
    width: 40px;
}

.avatar-post img {
    width: 100%;
    height: 100%;
    padding: 4px;
    border-radius: 50%;
    border: 2px solid blue;
}

.show-title-optionsPost {
    display: flex;
    flex-direction: column;
    text-align: center;
    border-radius: 50%;
    padding: 8px;
}

.show-title-optionsPost .svg-inline--fa {
    font-size: 20px;
    padding: 6px;
    display: flex;
    flex-direction: row;
    text-align: center;
    border-radius: 50%;
}

.show-title-optionsPost .svg-inline--fa:hover:hover {
    background-color: #E9EBEE;
}

.options-Post {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin-top: 8px;
    padding-bottom: 8px;
}

.options-Post .option {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2px 5px;
    /* background-color: #E9EBEE; */
}

.options-Post .option .svg-inline--fa {
    padding: 5px;
    width: 20px;
    height: 20px;
    border-radius: 8px;
    background-color: #E9EBEE;
}

.options-Post .option .title {
    font-size: 12px;
    font-weight: 500;
    margin-top: 4px;
}

