
.container-content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #F2F3F5;
}

.content {
    display: block;
}

.content:before {
    content: " ";
    position: relative;
    display: block;
    height: 56px;
    width: 100%;
}

.content-left {
    background-color: #E9EBEE;
    width: 250px;
    height: 1000px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.content-center {
    width: 800px;
    height: 1000px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #d6dbe3;
}


.content-center .container-center {
    width: 100%;
    height: 1000px;
    background-color: #E9EBEE;
    display: flex;
    flex-direction: column;
}

.content-center .navCenter-content {
    height: 44px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 8px;
    border: 1px solid #d6dbe3;
    /* position: fixed; */
    top: 56px;
    background-color: #fff;
}

.content-center .navCenter-content .container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.content-center .navCenter-content .svg-inline--fa {
    width: 20px;
    height: 20px;
    padding: 8px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 25%;
    color: #0d5585;
}

.content-center .navCenter-content .svg-inline--fa:hover {
    background-color: #E9EBEE;
}

.content-right {
    width: 325px;
    height: 1000px;
    background-color: #E9EBEE;
    display: flex;
    align-items: center;
    flex-direction: column;
}

@media (max-width: 501px) {
    .content-left, .content-right {
        display: none;
    }
    .container-content {
        padding: 0;
    }
    .container-content {
        width: 100% !important;
    }
    .content {
        width: 100%;
    }
    .content-center {
        width: 100%;
    }
}