/* reset Search Form */

.SearchForm .input-SearchForm {
    offset: 0;
    border: 0;
    outline: 0;
}

/* SearchForm */

.SearchForm {
    width: 100%;
    background-color: #FFFFFF;
    /* border: 1px solid black; */
    display: flex;
    height: 36px;
    border-radius: 20px;
    padding-right: 20px;
    padding-left: 20px;
}

.input-SearchForm {
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 16px;
}

/* css icon in search Form */

.SearchForm .svg-inline--fa {
    color: red;
    font-size: 20px;
    padding-top: 8px;
    padding-bottom: 8px;
}
